.sidebar {
    height:100vh;
    flex:0.2;
    background-color:#040404;
    color:white;
    height:100%;
    min-width:230px;
    padding:15px;
    padding-bottom:65px;
}

.sidebarLogo {
height:75px;
padding:10px;
margin-right: auto;
}

.sideBarTitle {
    margin-left:10px;
    padding:5px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width:90%;
    margin:10px auto;
}
