.header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}

.headerLeft {
    color:white;
    border-radius: 25px;
    display: flex;
    align-items: center;
    padding:10px;
    min-width: 80px;
}

.headerLeft  > input {
    background-color: transparent;
    border:none;
    width: 100%;
    color:white;
    flex:0.5;
    font-size: 17px;
    margin-left:5px;    
}
.headerLeft > input:focus {
    outline-width: 0;
}

.headerRight {
    display: flex;
    align-items: center;
}

.headerRight > h4 {
    margin-left: 10px;
}