.footer {
    position:fixed;
    display:flex;
    justify-content: space-between;
    bottom:0;
    height:60px;
    width:97%;
    background-color: #282828;
    padding:20px;
    color:white;
}

.footerGreen {
    color:#1ed15e;
}

.footerIcon:hover, .footerGreen:hover {
    transition: transform 0.2s ease-in-out;
    transform:scale(1.2) !important;
}
.footerRight * .MuiSlider-root {
    color:#1ed15e;
}

.footerLeft {
    flex:0.3;
    padding:0 100px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
}
.footerAlbumLogo {
    height:60px;
    width:60px;
    margin-right: 20px;
    object-fit: contain;
}
.footerCenter {
    flex:0.4;
    padding: 0 100px;
    display:flex;
    justify-content: space-between;
    max-width: 300px;
    align-items: center;
}

.footerRight {
    flex:0.3;
    padding:0 100px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
}