.login {
    display:grid;
    place-items: center;
    height:100vh;
    background-color: black;
}

.login > img {
    width:100%;
}

.login > a {
    padding:20px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 30px;
    background-color: #1db954;
    color:white;
    font-weight: 800;
    text-decoration: none;
}