.body {
    flex:0.8;
    height: 100%;
    background-color:#121212;
    color:white;
    padding:30px;
    overflow-y: overlay;
}

.bodyInfo {
    padding:10px;
    max-width: 650px;
}
.bodyInfo > img {
    height: 20vw;
    margin:0 20px;
}

.bodyInfoText {
    flex:1;
    margin:30px;
}

.bodyInfoText > h2 {
    font-size: 48px;
    margin-bottom: 10px;
}

.bodyInfoText > p {
    font-size: 14px;
}

.bodyIcons {
    display: flex;
    align-items: center;
    margin-left: 50px;
}
.bodyIcons > .MuiSvgIcon-root {
    margin-right:20px;
}
.bodySongs {
    margin:20px -30px;
}
.bodyShuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.bodyShuffle:hover {
    transition: 100ms transform ease-in;
    transform:scale(1.08);
}