.songrow {
    margin-left:20px;
    padding:20px;
    display: flex;
    align-items: center;
    color:white;
}

.songrow:hover {
    cursor: pointer;
    background-color: black;
    opacity: 0.65;
}

.songrowInfo {
    margin-left:20px;

}

.songrowInfo > h1 {
    font-size: 16px;
}
.songrowAlbum {
    width:40px;
    height: 40px;
    object-fit: contain;
}

.songrowInfo > p {
    font-size: 14px;
    margin-top: 3px;
    color:gray;
}